.list {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    border-radius: 7px;
    color: white;
}

.item {
    display: flex;
    flex-direction: column;
    gap: 5px;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    padding: 3% 10%;
    color: white;
    border-radius: 7px;
    background-color: #04AA6D;
    font-weight: 600;
}

.btn {
    padding: 3% 5%;
    font-size: 1.1rem;
    font-weight: 600;
    text-align: center;
    text-transform: capitalize;
    cursor: pointer;
    outline: none;
    color: #fff;
    background-color: #3e8e41;
    border: none;
    border-radius: 7px;
}

.btn:hover {
    background-color: #3e8e41;
}


